.Roadmap {
    width: 100%;
    height: 100%;
    display: flex;
    // overflow-x: hidden;
    flex-direction: column;
    justify-content: center;
    // align-items: center;
    gap: 150px;
    margin-top: 15vh;
    padding-bottom: 100px;
    // position: relative;

    .box-title-Roadmap {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 35px;
        width: 100%;

        h1 {
            font-family: 'Söhne Breit Test-regular';
            font-size: 50px;
            line-height: 50px;
            text-align: center;
            text-transform: uppercase;
            background: linear-gradient(92.61deg, #FF5836 0%, #FFFFFF 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            background-clip: text;
            text-fill-color: transparent;;
        }
    
        p {
            font-family: 'Nordique Pro';
            font-style: normal;
            font-weight: 400;
            font-size: 20px;
            line-height: 30px;
            text-align: center;
            color: #FFFFFF;
            width: 60%;
        }
    }

    .container-all {
        display: flex;
        width: 100%;
        justify-content: space-between;
        height: 100%;

        .container-counter {
            width: 40%;
            // height: 100%;
            display: flex;
            justify-content: center;


            .box-counter::before {
                content: "";
                width: 2px;
                height: 400px;
                left: -15%;
                position: absolute;
                background-color: #EE4200;
                display: inline-block;
            }


            .stickybox {
                position: sticky;
                top : 30%;
            }

            .box-counter {
                background: url("./../img/back-roadmap.png");
                background-size: 130% 130%;
                background-repeat: no-repeat;
                background-position: center;
                // width: 45%;
                max-width: 334px;
                height: 294px;
                display: flex;
                flex-direction: column;
                // position: relative;
                justify-content: center;
                align-items: center;
                box-shadow: -6px 6px 28px rgba(0, 0, 0, 0.47), inset 0px -2.50245px 1.25122px rgba(255, 255, 255, 0.1);
                backdrop-filter: blur(18.7684px);
                padding: 0 20px;
                gap: 30px;
                /* Note: backdrop-filter has minimal browser support */

                border-radius: 20px;
                transform: matrix(1, 0, 0, 1, 0, 0);
    
                h3 {
                    font-family: 'Söhne Breit Test2';
                    font-size: 20px;
                    line-height: 32px;
                    text-align: center;
                    color: #FFFFFF;
                    opacity: 0.88;
                    width: 70%;
                }
    
                .counter {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    gap: 20px;
                    width: 100%;
                }
    
                span {
                    font-family: 'Söhne Breit Test2';
                    font-size: 68px;
                    line-height: 42px;
                    font-weight: 700;
                    text-align: center;
                    color: #FFFFFF;
                    opacity: 0.88;
                    background: linear-gradient(156.02deg, rgba(84, 84, 84, 0.6091) 5.67%, rgba(0, 0, 0, 0.6091) 30.52%);
                    box-shadow: -6px 6px 28px rgba(0, 0, 0, 0.47), inset 0px -2.50245px 1.25122px rgba(255, 255, 255, 0.1);
                    backdrop-filter: blur(18.7684px);
                    width: 25%;
                    position: relative;
                    /* Note: backdrop-filter has minimal browser support */
    
                    border-radius: 20px;
                    transform: matrix(1, 0, 0, 1, 0, 0);
                    padding: 40px 0px;

                    hr {
                        position: absolute;
                        top: 50%;
                        width: 100%;
                        height: 1px;
                    }
                }
            }
        }
    }

    .container-Roadmap {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 60%;
        height: 100%;
        gap: 100px;
        position: relative;
        background-image: url("./../img/roadmap.png");
        background-position: center;
        background-repeat: no-repeat;
        background-size: 75% 100%;

        img {
        width: 100%;
        height: 100%;
        position: absolute;
        }

        .l1 {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 130px;
            margin-top: 50px;
            // margin-left: 50px;
        }

        .l1 > .box {
            width: 25vw;
        }

        .l2::after {
            position: absolute;
            display: inline-block;
            left: 30vw;
            content: "";
            background: rgba(122, 159, 159, 0.963834);
            filter: blur(140px);
            width: 250px;
            height: 250px;
            z-index: -1;
        }
        .l2 {
            width: 100%;
            display: flex;
            align-items: center;
            gap: 70px;
            padding-left: 50px;
        }
        .l2 > .box {
                width: 32vw;
                padding: 30px 30px;
        }

        .l3 {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            gap: 100px;
        }
        .l4 {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 100px;
            // margin-left: 7vw;
        }
        .l5::before {
            position: absolute;
            display: inline-block;
            left: 20vw;
            top: 50px;
            content: "";
            background: rgba(122, 159, 159, 0.963834);
            filter: blur(140px);
            width: 250px;
            height: 250px;
            z-index: -1;
        }

        .l5 {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            gap: 100px;
            margin-top: 100px;
            position: relative;
        }
        .l6 {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            gap: 150px;
            padding-left: 50px;
        }

        .l6 > .box p {
            width: 90%;
        }

        .case {
            border: 1px solid #FFFFFF;
            padding: 15px 70px;
            position: relative;
            z-index: 1;
            
            .back-color::before {
                width: 0;
            }
            .back-color {
                background: linear-gradient(90.88deg, #A02C00 0.75%, #EE4200 87.96%, #000000 99.25%);
                width: 0%;
                height: 100%;
                left: 0;
                top: 0;
                position: absolute;
                // animation: slidein 5s;
            }

            .back-color1::before {
                width: 0;
            }
            .back-color3::before {
                width: 0;
            }
            .back-color4::before {
                width: 0;
            }
            .back-color5::before {
                width: 0;
            }
            .back-color6::before {
                width: 0;
            }

            .back-color1 {
                width: 0%;
                background: linear-gradient(90.88deg, #A02C00 0.75%, #EE4200 87.96%, #000000 99.25%);
                height: 100%;
                left: 0;
                top: 0;
                position: absolute;
                z-index: -1;
                // animation: slidein 5s;
            }
            .back-color3 {
                width: 0%;
                background: linear-gradient(90.88deg, #A02C00 0.75%, #EE4200 87.96%, #000000 99.25%);
                height: 100%;
                left: 0;
                top: 0;
                position: absolute;
                z-index: -1;
                animation: slidein 5s;
            }
            .back-color4 {
                width: 0%;
                background: linear-gradient(90.88deg, #A02C00 0.75%, #EE4200 87.96%, #000000 99.25%);
                height: 100%;
                left: 0;
                top: 0;
                position: absolute;
                z-index: -1;
                animation: slidein 5s;
            }
            .back-color5 {
                width: 0%;
                background: linear-gradient(90.88deg, #A02C00 0.75%, #EE4200 87.96%, #000000 99.25%);
                height: 100%;
                left: 0;
                top: 0;
                position: absolute;
                animation: slidein 5s;
                z-index: -1;
            }
            .back-color6 {
                width: 0%;
                background: linear-gradient(90.88deg, #A02C00 0.75%, #EE4200 87.96%, #000000 99.25%);
                height: 100%;
                left: 0;
                top: 0;
                position: absolute;
                z-index: -1;
                animation: slidein 5s;
            }
            span {
                font-family: 'Söhne Breit Test';
                font-size: 16px;
                line-height: 19px;
                text-transform: uppercase;
                color: #FFFFFF;
                z-index: 10;
            }
        }

        @keyframes slidein {
            from { width: 0%;}
        }

        @keyframes slidein2 {
            from { width: 0%;}
            to { width: 10%;}
        }
        @keyframes slidein3 {
            from { width: 0%;}
            to { width: 20%;}
        }
        @keyframes slidein4 {
            from { width: 0%;}
            to { width: 30%;}
        }
        @keyframes slidein5 {
            from { width: 0%;}
            to { width: 40%;}
        }
        @keyframes slidein6 {
            from { width: 0%;}
            to { width: 50%;}
        }
        @keyframes slidein7 {
            from { width: 0%;}
            to { width: 60%;}
        }
        @keyframes fadein {
            from {
                opacity:0;
            }
            to {
                opacity:1;
            }
        }

        .box {
            background: linear-gradient(85.96deg, rgba(255, 255, 255, 0) -20.51%, rgba(255, 255, 255, 0.05) 26.82%, rgba(255, 255, 255, 0) 65.65%), rgba(196, 196, 196, 0.05);
            box-shadow: -6px 6px 28px rgba(0, 0, 0, 0.47), inset 0px -2.50245px 1.25122px rgba(255, 255, 255, 0.1);
            backdrop-filter: blur(15.5367px);
            // width: 533.02px;
            width: 29vw;
            // height: 256.52px;
            border-radius: 20px;
            transform: matrix(1, 0, 0, 1, 0, 0);
            display: flex;
            flex-direction: column;
            padding: 30px 50px;
            gap: 15px;

            h2 {
                font-family: 'Söhne Breit Test2';
                font-size: 23px;
                line-height: 27px;
                text-transform: uppercase;
                color: #FFFFFF;
            }

            p {
                font-family: 'Söhne Breit Test-regular';
                font-size: 14px;
                line-height: 21px;
                text-align: justify;
                color: #FFFFFF;
            }
        }
    }
}

@media only screen and (max-width: 1100px) {
    .Roadmap {
        overflow-x: hidden;
        .box-title-Roadmap {
            gap: 50px;
        
            p {
                width: 75%;
            }
        }

        .container-all {
            display: flex;
            width: 100%;
            flex-direction: column-reverse;
            justify-content: center;
            align-items: center;
            gap: 130px;
            height: 100%;
    
            .container-counter {
                width: 40%;
                display: flex;
                justify-content: center;
    
    
                .box-counter::before {
                    content: "";
                    width: 500px;
                    height: 2px;
                    left: -85px;
                    bottom: -30px;
                    position: absolute;
                    background-color: #EE4200;
                    display: inline-block;
                }
    
    
                .stickybox {
                    position: unset;
                    top : 30px;
                }
    
                .box-counter {
                    background: url("./../img/back-roadmap.png");
                    background-size: 130% 130%;
                    background-repeat: no-repeat;
                    background-position: center;
                    background-attachment: fixed;
                    // width: 45%;
                    max-width: 334px;
                    height: 294px;
                    display: flex;
                    flex-direction: column;
                    // position: relative;
                    justify-content: center;
                    align-items: center;
                    box-shadow: -6px 6px 28px rgba(0, 0, 0, 0.47), inset 0px -2.50245px 1.25122px rgba(255, 255, 255, 0.1);
                    backdrop-filter: blur(18.7684px);
                    padding: 0 20px;
                    gap: 30px;
                    /* Note: backdrop-filter has minimal browser support */
    
                    border-radius: 20px;
                    transform: matrix(1, 0, 0, 1, 0, 0);
                }
            }
        }
        .container-Roadmap  {
            width: 90%;

            .l1 {
                gap: 13vw;
            }

            .l1 > .box {
                width: 50%;
            }
            .l2 {
                gap: 10vw;
            }
            .l2 > .box {
                width: 50%;
            }
            .l3 {
                gap: 10vw;
            }
            .l3 > .box {
                width: 50%;
            }
            .l4 {
                gap: 10vw;
            }
            .l4 > .box {
                width: 50%;
            }
            .l5 {
                gap: 10vw;
            }
            // .l5::before {
            // }

            .l5 > .box {
                width: 50%;
            }
            .l6 {
                gap: 10vw;
            }
            .l6 > .box {
                width: 50%;
            }

            .box {
                width: 40%;
                padding: 30px 25px;
            }
        }
    }
}

