    }
}

@media only screen and (max-width: 600px) {
    .Roadmap {
        padding-bottom: 60px;

        .container-all {
            display: flex;
            width: 100%;
            flex-direction: column-reverse;
            justify-content: center;
            align-items: center;
            gap: 130px;
            height: 100%;
    
            .container-counter {
                width: 100%;
                // height: 100%;
                display: flex;
                justify-content: center;
                // background-color: #3a288d;
    
    
                .box-counter::before {
                    content: "";
                    width: 120%;
                    height: 2px;
                    left: -10%;
                    // right: 10%;
                    bottom: -30px;
                    position: absolute;
                    background-color: #EE4200;
                    display: inline-block;
                }
    
    
                .stickybox {
                    position: unset;
                    top : 30px;
                }
    
                .box-counter {
                    max-width: 60%;
                    height: auto;
                    gap: 15px;
                    padding: 20px 0;
                    backdrop-filter: blur(0);
                    background: url("./../img/back-roadmap.png");
                    background-size: 170% 170%;
                    background-position: center;
                    h3 {
                        font-size: 16px;
                        line-height: 26px;
                        width: 75%;
                    }
                    span {
                        font-size: 45px;
                        line-height: 32px;
                        font-weight: 700;
                        width: 30%;
                        padding: 30px 0px;
                    }
                }
            }
        }

        .container-Roadmap {
            position: relative;
            background-image: none;
            width: 100%;

            .l1 {
                gap: 25px;
            }
            .l2 {
                gap: 25px;
            }
            .l2::after {
                width: 120px;
                height: 120px;
            }
            .l3 {
                gap: 25px;
            }
            .l4 {
                gap: 25px;
            }
            .l5 {
                gap: 25px;
            }
            .l5::before {
                width: 100px;
                height: 100px;
                top: 0;
            }
            .l6 {
                gap: 25px;
            }

            .box {
                h2 {
                    font-size: 20px;
                    line-height: 23px;
                }

                p {
                    font-size: 14px;
                    line-height: 23px;
                }
            }
        }
    }
}


